.resume {
  position: relative;
  display: flex;
  width: 800px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 25px 0;
  font: normal 400 16px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #000000;
  border: 10px solid #2c39f2;
  border-radius: 5px;
  background-color: #ffffff; }

.resume__col-one {
  width: 35%;
  border-right: 3px solid #2c39f2; }

.resume__container {
  position: relative;
  margin: 0 25px;
  text-align: center; }

.resume__avatar {
  overflow: hidden;
  width: 125px;
  height: 125px;
  margin: 15px auto 40px;
  border-radius: 50%;
  box-shadow: 0 0 0 10px #ffffff, 0 0 0 13px #2c39f2;
  overflow: hidden; }
  .resume__avatar picture,
  .resume__avatar img {
    display: block;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0; }
  .resume__avatar:hover {
    box-shadow: 0 0 0 10px #ffffff, 0 0 0 13px #ff1553; }

.resume__name {
  position: relative;
  margin: 0;
  padding: 0 45px;
  font: normal 400 20px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #ff1553;
  text-align: center;
  text-transform: uppercase; }
  .resume__name::before, .resume__name::after {
    content: "";
    position: absolute;
    top: 50%;
    display: block;
    width: 40px;
    height: 3px;
    background-color: #2c39f2; }
  .resume__name::before {
    left: 0; }
  .resume__name::after {
    right: 0; }

.resume__surname {
  margin: 0;
  padding: 0 10px;
  font: normal 700 26px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  text-align: center;
  text-transform: uppercase; }

.resume__title {
  position: relative;
  display: inline-block;
  min-width: 150px;
  margin: 30px 0;
  padding: 0 20px;
  font: normal 700 16px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #2c39f2;
  text-align: center;
  text-transform: uppercase;
  border: 3px solid #2c39f2; }
  .resume__title::before {
    content: "";
    position: absolute;
    bottom: -3px;
    left: -3px;
    border-top: 5px solid #2c39f2;
    border-right: 10px solid #2c39f2;
    border-bottom: 5px solid transparent;
    border-left: 10px solid transparent;
    transform: translateY(100%); }

.resume__contact-list {
  text-align: left; }

.resume__contact-link {
  position: relative;
  display: inline-block;
  padding-left: 20px;
  color: #ff1553; }
  .resume__contact-link:hover {
    opacity: 0.7; }
  .resume__contact-link:active {
    opacity: 0.3; }
  .resume__contact-link::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 15px;
    height: 15px;
    transform: translateY(-50%); }
  .resume__contact-link--phone::before {
    background: top 4px left 2px/15px 15px no-repeat url("../img/phone.svg"); }
  .resume__contact-link--mail::before {
    background: top 0 left 0/15px 15px no-repeat url("../img/envelope.svg"); }
  .resume__contact-link--loc::before {
    background: top 0 left 0/15px 15px no-repeat url("../img/location.svg"); }

.resume__item {
  position: relative;
  padding-left: 20px; }
  .resume__item::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 5px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #2c39f2; }

.resume__text {
  margin: 0;
  padding: 0;
  text-indent: 1em;
  text-align: left; }

.resume__skills-item {
  margin-bottom: 10px;
  font-weight: 700;
  text-align: left;
  text-transform: uppercase; }

.resume__bar {
  position: relative;
  width: 100%;
  height: 12px;
  border: 3px solid #2c39f2;
  background-color: #f6f8fa; }
  .resume__bar::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background-color: #ff1553; }
  .resume__bar--s::after {
    width: 20%; }
  .resume__bar--m::after {
    width: 40%; }
  .resume__bar--l::after {
    width: 60%; }
  .resume__bar--xl::after {
    width: 80%; }
  .resume__bar--xxl::after {
    width: 100%; }
